import * as f from "../functions.js"
import { player } from "../player.js"
import effects from "../buffsManager.js"
import { generateCardDomElement } from "../functions/domFunctions.js"

export class basicSkillCardClass{
static generatedCardType = "skill"

  constructor(name , energyCost , addingBlock){
    this.name = name
    this.energyCost = energyCost
    this.addingBlock = addingBlock

    this.target = "friendly"
    this.type = "skill"
    generateCardDomElement(this)
  }
  defaultPlay(entity){
    this.effect(entity)
    this.discard()
  }
  discard(){
    f.discardCard(this)
  }

    addBlock(entity){
      f.gainBlock(entity ,this.addingBlock ) 
    }
}

export class defend extends basicSkillCardClass{
  constructor(energyCost){
    super("Defend" , 1 , 10)
  }
  play(entity){
    this.defaultPlay(entity)
  }
  effect(entity){
    this.addBlock(entity)
  }
}

export class card extends basicSkillCardClass{
  constructor(energyCost){
    this.energyCost = energyCost

    this.name = ""


  }
  play(){
    console.log(`${this.name}`);
  }
}