import {attack_icon , shield_icon} from "../dom.js"

export function render_basic_attack_icon(move) {
  const action_icon = move.entity.actionIcon
  const icon = attack_icon.cloneNode(true)
  icon.classList.remove("hidden")

  action_icon.innerText = ""
  action_icon.append(  icon, move.damage)
}

export function render_basic_deffence_icon(move) {
  const action_icon = move.entity.actionIcon
  const icon = shield_icon.cloneNode(true)
  icon.classList.remove("hidden")

  action_icon.innerText = ""
  action_icon.append(  icon, move.block)
}