import {attackFriendly,damageFormulas,gainBlock} from "../functions.js"
import {basicDamageFormula} from "../functions.js"
import * as entityIcons from "./entityIcons.js"

export class basic_attack_friendly{
  constructor(enemy , damage){
    this.entity = enemy
    this.damage = damage
    this.icon = "attack"
    // this.damageCounter = null "formula"
  }
  exe(){
    attackFriendly( this.entity , this)
  }
  render() {
    entityIcons.render_basic_attack_icon(this)
  }
}

export class basic_deffend{
  constructor(entity, block){
    this.entity = entity
    this.block = block
    this.icon = "shield"
  }
  exe(){
    gainBlock(this.entity , this.block)
  }
  render(){
    entityIcons.render_basic_deffence_icon(this)
  }
}

export class Op{
  constructor(enemy , damage){
    this.entity = enemy
    this.damage = damage
    this.icon = "attack",
    this.damageCounter = damageFormulas.opDamage
  }
  exe(){
    attackFriendly( this.entity , this)

  }
  render() {
    entityIcons.render_basic_attack_icon(this)
  }
}