
export function basicDamageFormula(recievingEntity , DealingEntity , initialDamage) {
  let finalDamage = (initialDamage + DealingEntity.str ) *
           (DealingEntity.atkMod * recievingEntity.defMod )
  return Math.round(finalDamage)
}

